import React from 'react'
import ReactMarkdown from 'react-markdown'
import people from '../content/output/people.json'

import Header from './header'
import Publications from './publications'
import Footer from './footer'

class Publication extends React.Component {
  static async getInitialProps(req) {
    const id = req.query.id
    return { id: id }
  }

  render() {
    const publication = require(`../content/output/publications/${ this.props.id }.json`)

    return (
      <div>
        <title>{ `Interactions Lab | University of Calgary` }</title>

        <Header current="People" />

        <div className="ui stackable grid">
          <div className="one wide column"></div>
          <div className="ten wide column centered">
            <div id="publication" className="category">
              <p><span className="ui LARGE inverted grey label">{ publication.series }</span></p>
              <h1>{ publication.title }</h1>
              <p className="meta">
              {
                publication.authors
                .reduce((prev, curr) => [prev, ', ', curr])
              }
              </p>

            </div>
            <div className="video-container" style={{ display: publication.embed ? 'block' : 'none' }} >
              <iframe
                className="embed"
                width="100%"
                height="315"
                src={`${publication.embed}?`}
                frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen={true}
                mozAllowFullScreen={true}
                msAllowFullScreen={true}
                oAllowFullScreen={true}
                webkitAllowFullScreen={true}
              ></iframe>
            </div>

            <ReactMarkdown
              source={publication.bodyContent}
              escapeHtml={false}
              linkTarget="_blank"
            />

          </div>
          <div className="one wide column"></div>
        </div>
        <Footer />
      </div>
    )
  }
}

export default Publication
